export type EventHandler<E> = (event: E) => void;

export class EventDispatcher<E> {
    private handlers: EventHandler<E>[] = [];

    handle(event: E) {
        for (let h of this.handlers) {
            h(event);
        }
    }

    register(handler: EventHandler<E>) {
        this.handlers.push(handler);
    }
}